.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
  width: 100vw;
  height: 100vh;
  background-color: #465362;

  & > div {
    gap: 20px;
  }

  .input,
  .output {
    display: flex;
    flex-direction: row;
  }

  .input {
    input,
    select {
      background-color: #f4fffd;
      padding: 10px;
      border-radius: 7px;
      border: 1px solid #011936;
      font-size: 1em;
    }

    input {
      width: 20vw;
    }
    select {
      width: 100px;
      cursor: pointer;
    }
  }

  .output {
    .word {
      background-color: #f4fffd;
      padding: 10px;
      border-radius: 7px;
      border: 1px solid #011936;
      width: 20vw;
      font-size: 1em;
    }

    button {
      background-color: #ed254e;
      padding: 10px;
      border-radius: 7px;
      border: 1px solid #011936;
      font-size: 1em;
      width: 100px;
      cursor: pointer;
    }
  }
}
